<template>
  <div>
    <h1>Socket </h1>
    <div>
      <v-btn @click="joinRoom('testroom')"> </v-btn>
      <v-btn @click="leaveRoom('testroom')"> </v-btn>
      <v-btn @click="sendMsg(1)"></v-btn>
      <v-btn @click="sendMsg(2)"></v-btn>
      <v-btn @click="sendMsg(3)"></v-btn>
    </div>
    <div>
      <v-text-field v-model="toId" label="ID"></v-text-field>
      <v-text-field v-model="userMsg" label=""></v-text-field>
      <v-btn @click="sendUser">  </v-btn>
    </div>
  </div>
  
</template>

<script>
  import { mapActions } from 'vuex';
  export default {
    name: "Home",
    data() {
      return {
        title : "",
        toId : "",
        userMsg : "",
      }
    },
    title() {
      return this.title;
    },
    socket() {
      return {
        'room:msg' : (data) => {
          console.log('room:msg', data);
        },
        'room:chat' : (data) => {
          console.log('room:chat', data);
          this.toId = data.fromId;
          this.userMsg = data.userMsg;
        }
      }
    },
    methods: {
      ...mapActions('socket', ["joinRoom", "leaveRoom"]),
      sendMsg(target) {
        this.$socket.emit('room:send', {msg : target + ' Send Message', target : target});
      },
      sendUser() {
        const { toId, userMsg } = this;
        const { user } = this.$store.state.user;
        console.log( toId, userMsg, user);
        if (toId && userMsg && user) {
          this.$socket.emit('room:chat', { 
            toId, 
            userMsg,
            fromId : user.user_id
          });
        }
      }
    }
  }
</script>
